import * as React from 'react'
import { useCallback, useMemo, useState } from "react";
import { Virtuoso } from '../src'
import { ComputeItemKey } from "../dist";

function generateItems(length: number) {
  return Array.from({ length }, (_, index) => `My Item ${index}`)
}

const itemContent = (_: number, data: string) => {
  return <div style={{ height: 30 }}>{data}</div>
}
export default function App() {
  const [data, setData] = useState(() => generateItems(100))
  const parkedItems = useMemo(() => {
    return [data[20]]
  }, [data]);

  const computeItemKey: ComputeItemKey<string, unknown> = useCallback((_, data) => {
    return data
  }, [])

  return (
    <div>
      <button
        onClick={() => {
          setData((prevData) => {
            return generateItems(prevData.length + 20)
          })
        }}
      >
        Append 20 Items
      </button>
      <Virtuoso
        data={data}
        itemContent={itemContent}
        style={{ height: 300 }}
        parkedItems={parkedItems}
        computeItemKey={computeItemKey}
      />
    </div>
  )
}
